.main {
  width: 40vw;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #3c4144;
  display: inline-block;
  border-left: 5px solid black;
  overflow: scroll;
}

.shopHeader {
  padding: 1rem 0;
  text-align: center;
  color: white;
  font-size: 2rem;
}

@media (max-width: 800px) {
  .main {
    width: 100%;
    overflow: visible;
    border-top: 5px solid black;
    border-left: none;
  }
}
